
























































































































export default {
	data() {
		return {
			indexData:{}//首页数据源
		}
	},
	onLoad() {
		this.getDataList();
	},
	methods: {
		getDataList(){
			const _this = this;
			this.$lotusUtils.ajax(`${this.$lotusUtils.webUrl.api}`,'POST',{
				uri:"Index/all",
				options:{}
			}).then((response)=>{
				const res = response.data;
				if(res){
					_this.indexData = res;
					_this.bannerImages = res.banner;
				}
				console.log(res);
			})
		}
	}
}
